  .m-global-eyebrow .a-tagline,
  .site-header,
  .ds-footer {
    padding: 0;
  }
}

main {
  max-width: 1170px;
  margin: 30px auto;
  padding: 0 30px;
}

.ds-sidebar,
.ds-content {
  display: table-cell;
  vertical-align: top;
}

.ds-sidebar {
  width: 250px;
  padding-right: 30px;
  border-right: 1px solid var(--gray-40);
  padding-bottom: 30px;
}

.ds-content {
  padding-left: 30px;
  width: 75%;
}
.ds-content li,
.ds-content p {
  max-width: 41.875rem;
}

.ds-footer {
  padding-bottom: 30px;
}
.ds-footer__content {
  max-width: 41.875rem;
}
.ds-footer a {
  border-bottom-width: 1px;
}

@media only all and (max-width: 37.5em) {
  main {
    padding: 0;
    margin-top: 0;
  }
  .ds-sidebar,
  .ds-content {
    display: block;
  }
  .ds-content {
    padding-left: 15px;
  }
  .ds-sidebar {
    width: auto;
    border-right: none;
    padding-right: 0;
  }
  .m-global-eyebrow,
  .ds-footer {
    padding: 0 15px;
  }
